import { Column, Entity, OneToMany, ManyToOne } from 'typeorm';
import { rules } from 'validation-pkg';

import { BaseEntity } from './base.entity';
import { User } from './user.entity';
import { PlaylistToMusic } from './playlist-to-music.entity';

@Entity()
export class Music extends BaseEntity {

  @Column('varchar', { length: rules.music.title.maxLength })
  public title!: string;

  @Column('varchar', { length: rules.music.link.maxLength })
  public link!: string;

  @Column('simple-array')
  public artists?: string[];

  @Column('varchar', { length: rules.music.album.maxLength })
  public album?: string;

  @Column('simple-json')
  // TODO type
  public source?: string;

  @Column('varchar', { length: rules.music.thumbnail.maxLength })
  public thumbnail?: string;

  @Column('simple-array')
  public tags!: string[];

  @Column('float', { unsigned: true })
  public duration!: number;

  @Column('boolean')
  public approved!: boolean;

  @Column('float')
  public correctness?: number;

  @OneToMany(() => PlaylistToMusic, playlistToMusic => playlistToMusic.playlist)
  public playlistToMusics!: PlaylistToMusic[];

  @Column('int', { unsigned: true })
  public userId!: number;

  @ManyToOne(() => User, user => user.musics)
  public user!: User;
}
